.fondo {
  background-image: url("/build/img/fondo.jpg");
  background-size: contain; /* Ajusta la imagen al tamaño de la ventana del navegador */
}

#fondo {
  background-color: rgba(32, 32, 32, 0.5882352941) !important;
}

.navbar {
  justify-content: flex-end;
}

.navbar-brand {
  display: flex;
  align-items: center;
}

.navbar-brand img {
  width: 30px;
  margin-right: 100px; /* Espacio entre el ícono y el texto */
}

.img {
  border: 1px solid #ccc;
}

/* FOOTER */
body {
  margin: 0;
  padding: 0;
  background-color: #f5f5f5;
}

header {
  background-color: #693d28;
  color: #fff;
  padding: 20px;
  text-align: center;
}

footer {
  background-color: #693d28;
  color: #fff;
  text-align: center;
  padding: 20px 0;
}

footer p {
  margin: 0;
}

.footer__color p {
  color: #fff;
}

.footer-images {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.footer-images img {
  margin: 0 10px;
  width: 50px;
  height: 50px;
}

.contact-info {
  margin: 20px 0;
  font-size: 1.2em;
}

/* animaciones */
@keyframes Desliza_abajo {
  0%, 100% {
    transform: scale(1.5);
  }
  50% {
    transform: scale(1);
  }
}
.custom-animation {
  animation: Desliza_abajo 1.8s ease-in-out infinite; /* Duración de 2 segundos, rebote lento y animación infinita */
}

.container-fluid {
  margin-top: 25px; /* Espacio para visualizar la animación */
  margin-block-end: 25px;
}

/* Estilos CSS para el contenedor de información */
.about-us-container {
  background-color: #D2B48C; /* Color café claro */
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  width: 1300px;
  margin: 0 auto;
  text-align: center;
}

/* Estilos CSS para el título */
.about-us-title {
  font-size: 24px;
  font-weight: bold;
  text-align: center;
}

/* Estilos CSS para la descripción */
.about-us-description {
  font-size: 16px;
  text-align: justify;
  margin: 20px 0;
}

/* Estilos CSS para el botón */
.learn-more-button {
  background-color: #323235;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 18px;
  cursor: pointer;
}

/* Estilos para seccion de mapas */
.contact-container {
  background-color: #D2B48C;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  width: 800px;
  margin: 0 auto;
  text-align: center;
}

.contact-title {
  font-size: 24px;
  font-weight: bold;
}

.contact-address {
  font-size: 16px;
  margin: 20px 0;
}

.contact-image {
  max-width: 100%;
  height: auto;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, .heading-blanco, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #5e311e;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2, .heading-blanco {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #5e311e;
  font-size: 2rem;
}

.text-center {
  text-align: center;
}

.nombre-pagina {
  margin: 5rem 0;
  font-weight: 700;
}

h1 {
  font-size: 4.6rem;
  color: #FFFFFF;
}
h1 span {
  color: #c39c63;
  font-size: 2.8rem;
  display: block;
}
h1::after {
  content: "";
  display: block;
  width: 10rem;
  height: 10rem;
  margin: 0 auto;
  background-image: url(../img/cafe.svg);
  background-size: 10rem;
}

.heading-blanco {
  color: #FFFFFF;
}
.heading-blanco span {
  color: #FFFFFF;
}
.heading-blanco::after {
  display: none;
}

h3 {
  font-size: 3.2rem;
}

.heading-blanco {
  color: #FFFFFF;
}
.heading-blanco span {
  color: #FFFFFF;
}
.heading-blanco::after {
  display: none;
}

.verificacion__texto p {
  text-align: center;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 3rem;
}
.verificacion__imagen {
  display: flex;
  justify-content: center;
  align-items: center;
}
.verificacion__imagen img {
  margin: auto;
  border-radius: 60%;
  max-width: 50rem;
}

.header {
  padding: 5rem 0;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .header {
    min-height: 2rem;
  }
}
.header::before {
  content: "";
  background-color: #1a1b15;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.6;
}
.header .contenido-header {
  position: relative;
}
.header .contenido-header h1 {
  text-align: center;
  color: #FFFFFF;
  text-transform: uppercase;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .header .contenido-header h1 {
    margin-top: 10rem;
  }
}

.header-nosotros {
  background-image: url(../img/header_nosotros.jpg);
}

.header-proceso {
  background-image: url(../img/header_proceso.jpg);
}

.header-menu {
  background-image: url(../img/header_menu.jpg);
}

.header-galeria {
  background-image: url(../img/header_galeria.jpg);
}

.header-contacto {
  background-image: url(../img/header_contacto.jpg);
}

.nav-principal {
  display: flex;
  flex-direction: column;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .nav-principal {
    flex-direction: row;
    gap: 1rem;
    margin-top: 0;
  }
}
.nav-principal a {
  text-align: center;
  color: #FFFFFF;
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .nav-principal a {
    margin-bottom: 0;
  }
}
.nav-principal a:hover {
  color: #c39c63;
}
.nav-principal a.activo {
  color: #c39c63;
}

.barra {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.barra .logo {
  width: 50%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .barra .logo {
    width: 20rem;
    margin: 0;
  }
}

.footer {
  background-color: #5e311e;
  margin-top: 5rem;
}
.footer .contenido-footer {
  padding: 5rem;
}
@media (min-width: 768px) {
  .footer .contenido-footer {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.footer .copyright {
  text-align: center;
  margin: 0;
  padding: 2rem;
  color: #FFFFFF;
}

.footer-box {
  font-family: "Poppins", sans-serif;
  text-align: center;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .footer-box {
    margin-bottom: 0;
  }
}
.footer-box h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 1rem 0;
  font-size: 2.4rem;
  color: #c39c63;
}
.footer-box p, .footer-box a {
  font-size: 2rem;
  margin: 0;
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.contenedor-app .imagen {
  height: 20rem;
  position: relative;
  background-image: url("../img/log.jpg");
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: auto;
  }
}
.contenedor-app .imagen::before {
  content: "";
  background-color: #1a1b15;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.1;
}
.contenedor-app .app {
  width: 95%;
  margin: 0 auto;
}
.contenedor-app .app p {
  margin-bottom: 4rem;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  color: #c39c63;
}

.seccion {
  display: none;
}
.seccion h2 {
  font-family: "Poppins", sans-serif;
  font-size: 2.5rem;
  font-weight: 700;
  padding-top: 5rem;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.listado-servicios {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios .servicio {
  background-color: #1a1b15;
  padding: 3rem;
  border: #c39c63 solid 0.2rem;
  border-radius: 1rem;
  transition-property: transform;
  transition-duration: 300ms;
  text-align: center;
  line-height: 0.8;
}
.listado-servicios .servicio.seleccionado {
  background-color: #c39c63;
}
.listado-servicios .servicio.seleccionado p {
  color: #1a1b15;
}
.listado-servicios .servicio:hover {
  transform: scale(1.1);
}
.listado-servicios .servicio p {
  color: #FFFFFF;
  margin: 0 0 1.25rem 0;
}
.listado-servicios .servicio .precio-servicio {
  color: #c39c63;
  font-weight: 700;
  font-size: 3rem;
}
.listado-servicios .servicio .descripcion-servicio {
  color: #5e311e;
  font-weight: 700;
}

.contenido-resumen p {
  margin-bottom: 1rem;
}
.contenido-resumen span {
  color: #c39c63;
  font-weight: 700;
  font-size: 2.4rem;
}

.contenedor-servicio {
  border-bottom: 1px solid #e1e1e1;
  padding: 3rem 0;
}

.formulario {
  width: 100%;
}
.formulario .campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.formulario .campo label {
  flex: 0 0 10rem;
  color: #5e311e;
  font-family: "Poppins", sans-serif;
  font-weight: 300;
  font-size: 1.5rem;
}
.formulario .campo input {
  flex: 1;
  padding: 1.25rem;
  border: 0.5rem solid #5e311e;
  border-radius: 1rem;
  font-size: 1.5rem;
}
.formulario .campo input:hover {
  background-color: #e1e1e1;
}
.formulario .campo input:disabled {
  background-color: #4d4d4d;
  color: #c39c63;
  cursor: not-allowed;
}

.boton {
  background-color: #5e311e;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border-radius: 1rem;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #371d12;
  cursor: pointer;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  color: #5e311e;
  display: block;
  margin-bottom: 2.5rem;
  text-align: center;
}

.alerta {
  padding: 1.25rem;
  color: #FFFFFF;
  width: 100%;
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  border-radius: 1rem;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #329f00;
}

@media (min-width: 768px) {
  .tabs {
    display: flex;
  }
  .tabs button {
    padding: 1rem;
  }
}
.tabs button {
  background-color: #5e311e;
  border: none;
  color: #FFFFFF;
  padding: 1rem;
  display: block;
  width: 100%;
  margin-bottom: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  transition: background-color 300ms ease-in-out;
}
.tabs button:hover {
  background-color: black;
}
.tabs button.actual {
  background-color: #c39c63;
  color: #FFFFFF;
}

.paginacion {
  padding: 5rem;
}
@media (min-width: 1024px) {
  .paginacion {
    display: flex;
    justify-content: space-between;
  }
}
.paginacion button {
  margin: 0 0 1rem 0;
}

.ocultar {
  visibility: hidden;
}

.contenido-menu {
  background-image: url(../img/cafe.jpg);
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: 100%;
  aspect-ratio: 3/6;
}
@media (min-width: 480px) {
  .contenido-menu {
    aspect-ratio: 3/4;
  }
}
@media (min-width: 768px) {
  .contenido-menu {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    aspect-ratio: 16/6;
    background-size: 40%;
    background-position: bottom right;
  }
}
.contenido-menu .texto p {
  font-size: 2rem;
}

.nuestro-menu {
  background-image: url(../img/menu_arriba.svg), url(../img/menu_abajo.svg), linear-gradient(to top, #5e311e, #5e311e);
  background-repeat: no-repeat;
  background-position: center 5rem, center 92%, 100%;
  background-size: 200%;
  padding: 15rem 2rem 20rem 2rem;
}
@media (min-width: 768px) {
  .nuestro-menu {
    padding: 15rem 0 20rem 0;
    background-size: 100%;
  }
}
@media (min-width: 1024px) {
  .nuestro-menu {
    background-size: 50%, 50%, 100%;
  }
}

@media (min-width: 768px) {
  .grid-menu {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.menu h3 {
  color: #FFFFFF;
}
.menu li {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.menu li p {
  color: #FFFFFF;
  font-size: 2.2rem;
  line-height: 1.5;
  margin: 0 0 2rem 0;
}
.menu li p span {
  display: block;
  color: #c39c63;
}
.menu li .precio {
  font-size: 4rem;
  font-weight: 700;
}